////Стили точек под модалкой////
.movie-buttons_film {
  /* gap: 10px; */
  /* align-items: center; */

  @include minBreakpoints($mobile) {
    /* align-items: center; */
    display: block;
    
  }

  @include minBreakpoints($tablet) {
    display: flex;
    gap: 8px;
    align-items: center;
  }

  @include minBreakpoints($desktop) {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  .button_film,
  .trailer_btn {
    display: inline-block;
    min-width: 110px;
    padding: 14px 10px;

    font-family: 'Roboto';
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;

    color: #000000;
    background-color: white;
    border: 1px solid #000000;
    border-radius: 5px;
    cursor: pointer;
    user-select: none;
  }

  &__input:checked + &__text,
  .button_film:hover {
    color: white;
    background-color: #ff6b01;
    border: 1px solid #ff6b01;
  }
  &__input {
    display: none;
  }
}

.basicLightbox:after {
  content: '';
  position: absolute;
  top: 1.8rem;
  right: 1.8rem;
  width: 2em;
  height: 2em;
  background: url(https://basiclightbox.electerious.com/assets/images/close.svg);
  background-size: contain;
  background-repeat: no-repeat;
  overflow: hidden;

  @include minBreakpoints($mobile) {
    top: 0.5rem;
    right: 0.5rem;
  }

  @include minBreakpoints($tablet) {
    top: 1.8rem;
    right: 1.8rem;
  }

  @include minBreakpoints($desktop) {
    top: 1.8rem;
    right: 1.8rem;
  }
}

